import React from "react";
import {
  Container,
  MDBBtn,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Button
} from "mdbreact";
import { Input } from "mdbreact";

class Join extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal
    });
  };

  render() {
    return (
      <div>
        <MDBBtn outline color="info" onClick={this.toggle}>
          
        </MDBBtn>
        <Modal isOpen={this.state.modal} toggle={this.toggle} position="top">
          <ModalHeader
            className="text-center"
            titleClass="w-100 font-weight-bold"
            toggle={this.toggle}
          >
            
          </ModalHeader>
          <ModalBody>
            <form className="mx-3 grey-text">
              <Input
                label=""
                icon="user"
                group
                type="text"
                validate
                error="wrong"
                success="right"
              />
              <Input
                label=""
                icon="lock"
                group
                type="password"
                validate
              />
              <Input
                label=" "
                icon="lock"
                group
                type="password"
                validate
              />
              <Input
                label=""
                icon="user"
                group
                type="text"
                validate
                error="wrong"
                success="right"
              />
              <Input
                label=""
                icon="user"
                group
                type="text"
                validate
                error="wrong"
                success="right"
              />
              <Input
                label=""
                icon="envelope"
                group
                type="email"
                validate
                error="wrong"
                success="right"
              />
              <Input
                label=""
                icon="tag"
                validate
                error="wrong"
                success="right"
              />
            </form>
          </ModalBody>
          <ModalFooter className="justify-content-center">
            <Button color="primary"></Button>
            <Button color="secondary" onClick={this.toggle}>
              
            </Button>{" "}
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default Join;
